import React from 'react';
import {BrowserRouter as Link} from "react-router-dom";
import {Html5Outlined} from '@ant-design/icons';
import UsePageViews from '../helpers/UsePageViews';
import { Menu} from 'antd';
const SidebarMenu = props => {
    function checkCategory() {
        if (props.category === "html") {
            return (
                <Menu theme="dark">
                    <Menu.Item
                        className={`${UsePageViews() === '/introHTML'
                        ? 'ant-menu-item-selected'
                        : ''}`}
                        key="1"
                        icon={< Html5Outlined />}>
                        <Link to='htmlMain/introHTML'>Введение</Link>
                    </Menu.Item>
                    <Menu.Item
                        className={`${UsePageViews() === '/html'
                        ? 'ant-menu-item-selected'
                        : ''}`}
                        key="2"
                        icon={< Html5Outlined />}>
                        <Link to='htmlMain/html'>htmlFirst</Link>
                    </Menu.Item>
                    <Menu.Item
                        className={`${UsePageViews() === '/moreHTML'
                        ? 'ant-menu-item-selected'
                        : ''}`}
                        key="3"
                        icon={< Html5Outlined />}>
                        <Link to='htmlMain/moreHTML'>htmlSecond</Link>
                    </Menu.Item>
                </Menu>
            )
        }
        if (props.category === "css") {
            return (
                <div className="nice">
                    <Menu theme="dark">
                        <Menu.Item
                            className={`${UsePageViews() === '/introCSS'
                            ? 'ant-menu-item-selected'
                            : ''}`}
                            key="1"
                            icon={< Html5Outlined />}>
                            <Link to='/cssMain/introCSS'>Введение</Link>
                        </Menu.Item>
                        <Menu.Item
                            className={`${UsePageViews() === '/css'
                            ? 'ant-menu-item-selected'
                            : ''}`}
                            key="2"
                            icon={< Html5Outlined />}>
                            <Link to='/cssMain/css'>cssFirst</Link>
                        </Menu.Item>
                        <Menu.Item
                            className={`${UsePageViews() === '/morecss'
                            ? 'ant-menu-item-selected'
                            : ''}`}
                            key="3"
                            icon={< Html5Outlined />}>
                            <Link to='/cssMain/morecss'>cssSecond</Link>
                        </Menu.Item>
                    </Menu>
                </div>
            )
        }

    }
    return (
        <div>
            {checkCategory()}
        </div>
    );
};

export default SidebarMenu;