import React, {useEffect} from 'react';
import ReactDOM from 'react-dom';
import 'antd/dist/antd.css';
import {Layout, Menu} from 'antd';
import {BrowserRouter as Router, Switch, Route, Link, useLocation} from "react-router-dom";
import {HashRouter} from "react-router-dom";
import IntroductionCSS from './cssArticles/IntroductionCSS'
import IntroductionHTML from './htmlArticles/IntroductionHTML'
import SidebarMenu from '../components/SidebarMenu';
const {Header, Content, Footer, Sider} = Layout;

function Social() {
    return (
        <div
            style={{
            color: 'white',
            padding: '20px'
        }}>
            Find us
        </div>
    );
}
function HomePage() {
    return (
        <div>
            Welcome page
        </div>
    );
}
function NotFoundPage() {
    return (
        <div>
            oh that page not found sorry
        </div>
    );
}

function SiderDemo() {
    return (
        <HashRouter>
            <Layout style={{
                minHeight: '100vh'
            }}>
                <Sider collapsible>
                    <div className="logo"/>
                    <Menu theme="dark" mode="horizontal">
                        <Menu.Item key="1">
                            <Link to='/htmlMain'>HTML</Link>
                        </Menu.Item>
                        <Menu.Item key="2">
                            <Link to='/cssMain'>CSS</Link>
                        </Menu.Item>

                    </Menu>
                    <Switch>
                        <Route exact path='/' render={props => <Social/>}/>
                        <Route path='/htmlMain' render={props => <SidebarMenu category='html'/>}/>
                        <Route path='/cssMain' render={props => <SidebarMenu category='css'/>}/></Switch>
                </Sider>
                <Layout className="site-layout">
                    <Header
                        className="site-layout-background"
                        style={{
                        padding: 0
                    }}/>

                    <Content
                        style={{
                        margin: '0 16px'
                    }}>

                        <Switch>
                            <Route exact path='/' component={() =>< HomePage />}/>
                            <Route path='/cssMain' component={() =>< IntroductionCSS />}/>
                            <Route path='/htmlMain' component={() =>< IntroductionHTML />}/>
                            <Route component={() =>< NotFoundPage />}/>
                        </Switch>
                    </Content>
                    <Footer
                        style={{
                        textAlign: 'center'
                    }}>
                        dev blog</Footer>
                </Layout>
            </Layout>
        </HashRouter>
    );
}
export default SiderDemo;